<template>
  <div></div>
</template>

<script>
import jsonData from "../choices.json";

export default {
  data() {
    return {
      params: jsonData.params,
      choicesPool: jsonData.choices,
      choicesDisabled: jsonData.choicesDisabled,
      choicesOnCooldown: jsonData.choicesOnCooldown
    };
  },
  methods: {
    resetParser() {
      this.params = jsonData.params;
      this.choicesPool = jsonData.choices;
      this.choicesDisabled = jsonData.choicesDisabled;
      this.choicesOnCooldown = jsonData.choicesOnCooldown;
    },
    updateParser(choiceMade, time) {
      this.updateParams(choiceMade);
      this.updateTime(time);
      this.updateChoicesCd(choiceMade.duration);
      this.putChoiceOnCd(choiceMade);
    },
    updateParams(choiceMade) {
      if (Object.keys(choiceMade.params).length !== 0) {
        for (const param of choiceMade.params) {
          if (param.op === "=") {
            this.params[param.name] = param.value;
          }
        }
      }
    },
    updateTime(time) {
      this.params["day"] = time.day;
      this.params["hours"] = time.hour;
    },
    updateChoicesCd(duration) {
      for (let i = this.choicesOnCooldown.length - 1; i >= 0; i--) {
        this.choicesOnCooldown[i].cdCount -= duration;
        if (this.choicesOnCooldown[i].cdCount <= 0) {
          this.choicesPool.push(this.choicesOnCooldown.splice(i, 1)[0]);
        }
      }
    },
    putChoiceOnCd(choice) {
      if (choice.cooldown !== 0) {
        this.choicesPool.splice(this.IndexById(this.choicesPool, choice.id), 1);
        if (choice.cooldown === -1) {
          this.choicesDisabled.push(choice);
        } else {
          choice.cdCount = choice.cooldown;
          this.choicesOnCooldown.push(choice);
        }
      }
    },
    updateChoices(choicesCurrent) {
      var choicesUpdated = this.getChoicesFromPool();
      var choicesToRemove = [];

      var choiceIndex;
      for (let i = choicesCurrent.length - 1; i >= 0; i--) {
        choiceIndex = this.IndexById(choicesUpdated.pass, choicesCurrent[i].id);
        if (choiceIndex !== -1) {
          choicesUpdated.pass.splice(choiceIndex, 1);
        } else {
          choiceIndex = this.IndexById(
            choicesUpdated.failInit,
            choicesCurrent[i].id
          );
          if (choiceIndex === -1) {
            choicesToRemove.push(choicesCurrent[i]);
          }
        }
      }
      var choicesToAdd = choicesUpdated.pass;

      return {
        current: choicesCurrent,
        toAdd: choicesToAdd,
        toRemove: choicesToRemove
      };
    },
    getChoicesFromPool() {
      var choices = { pass: [], failInit: [] };
      for (const choice of this.choicesPool) {
        if (this.checkConditions(choice.conditions)) {
          if (this.checkConditions(choice.conditionsInit)) {
            choices.pass.push(choice);
          } else {
            choices.failInit.push(choice);
          }
        }
      }
      return choices;
    },
    checkConditions(conditions) {
      for (const condition of conditions) {
        if (condition.op === "==") {
          if (!this.checkEqual(condition)) {
            return false;
          }
        }
      }
      return true;
    },
    checkEqual(condition) {
      if (this.params[condition.name] === condition.value) {
        return true;
      } else {
        return false;
      }
    },
    IndexById(array, id) {
      for (const [i, object] of array.entries()) {
        if (object.id === id) {
          return i;
        }
      }
      return -1;
    }
  }
};
</script>
