<template>
  <template v-for="choice in this.choices.current" :key="choice.id">
    <template v-if="isToRemove(choice)"> </template>
    <template v-else>
      <button @click="onClick(choice)">
        {{ choice.title }}
      </button>
      <br />
    </template>
  </template>
  <template v-for="choice in this.choices.toAdd" :key="choice.id">
    <button @click="onClick(choice)" class="toAdd">
      {{ choice.title }}
    </button>
    <br />
  </template>
</template>

<script>
export default {
  name: "c-choice-display",
  props: {
    choices: {}
  },
  computed: {},
  emits: ["choice-made"],
  methods: {
    onClick(choice) {
      this.$emit("choice-made", choice);
    },
    isToRemove(choice) {
      if (this.IndexById(this.choices.toRemove, choice.id) === -1) {
        return false;
      } else {
        return true;
      }
    },
    IndexById(array, id) {
      for (const [i, object] of array.entries()) {
        if (object.id === id) {
          return i;
        }
      }
      return -1;
    }
  }
};
</script>

<style>
button {
  background: black;
  border-color: white;
  border: none;
  color: white;
  font-size: 4vh;
}
button.toRemove {
  color: red;
}
button.toAdd {
}
</style>
