<template>
  <c-parser ref="parser" />
  <cTextDisplay :lines="linesDisplayed" :time="time" />
  <div id="lower">
    <c-choice-display :choices="choices" @choice-made="choiceMade" />
  </div>
</template>

<script>
import cChoiceDisplay from "./components/c-choice-display.vue";
import cParser from "./components/c-parser.vue";
import cTextDisplay from "./components/c-text-display.vue";

export default {
  name: "app",
  components: {
    cTextDisplay,
    cChoiceDisplay,
    cParser
  },
  mounted() {
    this.time = {
      day: this.getRandomInt(1, 7),
      hour: this.getRandomInt(0, 23)
    };
    this.$refs.parser.updateTime(this.time);
    this.choices.current = this.$refs.parser.getChoicesFromPool().pass;
  },
  data() {
    return {
      choices: {
        current: [],
        toAdd: [],
        toRemove: []
      },
      linesDisplayed: [
        {
          choice: "Nothing",
          text:
            "Vous me dîtes que vous êtes un héro... Très bien, très bien... Mais quel genre de héro ?"
        }
      ],
      maxChoice: 12,
      time: { day: 1, hour: 0 }
    };
  },
  methods: {
    choiceMade(choice) {
      this.applyUpdates();

      this.linesDisplayed.unshift({ choice: choice.title, text: choice.text });

      this.updateTime(choice.duration);

      this.$refs.parser.updateParser(choice, this.time);
      this.choices = this.$refs.parser.updateChoices(this.choices.current);

      if (this.choices.current.length > this.maxChoice) {
        this.gameOver();
      }
    },
    applyUpdates() {
      for (let i = this.choices.toRemove.length - 1; i >= 0; i--) {
        this.choices.current.splice(
          this.IndexById(this.choices.current, this.choices.toRemove[i].id),
          1
        );
      }
      this.choices.current = this.choices.current.concat(this.choices.toAdd);
    },
    updateTime(duration) {
      this.time.hour += duration;

      if (this.time.hour > 23) {
        this.time.day += Math.floor(this.time.hour / 23);
        this.time.hour %= 23;
        if (this.time.day > 7) {
          this.time.day %= 7;
        }
      }
    },
    gameOver() {
      this.choices = {
        current: [],
        toAdd: [],
        toRemove: []
      };
      this.cTextDisplay = "game over";
      this.$refs.parser.resetParser();
    },
    IndexById(array, id) {
      for (const [i, object] of array.entries()) {
        if (object.id === id) {
          return i;
        }
      }
      return -1;
    },
    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    }
  }
};
</script>

<style>
#app {
  position: absolute;
  text-align: center;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

#lower {
  position: fixed;
  bottom: 0;
  background-color: black;
  width: 100%;
  transition: offsetHeight 2s;
}
</style>
