<template>
  <div id="text-display">
    <div id="timer">
      <!-- <h1>{{ this.day(time.day) + " - " + this.hour }}</h1> -->
      <h1>La voie du Héro</h1>
    </div>
    <div ref="panel">
      <p v-for="(line, index) in lines" :key="index">
        <!-- {{ line.choice + " / " + line.text }} -->
        {{ line.text }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "c-text-display",
  props: {
    lines: {
      type: Array
    },
    time: {
      type: Object
    }
  },
  computed: {
    hour() {
      var minutes = this.getRandomInt(0, 59);
      if (minutes < 10) {
        minutes = "0" + minutes;
      }
      return this.time.hour + ":" + minutes;
    }
  },
  methods: {
    day(index) {
      switch (index) {
        case 1:
          return "Monday";
        case 2:
          return "Thuesday";
        case 3:
          return "Wednesday";
        case 4:
          return "Tuesday";
        case 5:
          return "Friday";
        case 6:
          return "Saturday";
        case 7:
          return "Sunday";
        default:
          break;
      }
    },
    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1)) + min;
    }
  },
  watch: {
    lines: {
      handler() {
        // this.$refs.panel.scrollTo(0, 0);
      },
      deep: true
    }
  }
};
</script>

<style>
#text-display {
  /* width: 60vh; */
  margin-left: 20vw;
  margin-right: 20vw;
}
#timer {
  text-align: center;
}
p {
  font-size: 3vh;
  text-align: left;
}
</style>
